header {
  display: flex;
  align-items: center;
  position: fixed;
  width: 100%;
}
nav {
  display: flex;
  justify-content: space-between;
  position: relative;
  align-items: center;
  width: 100%;
  height: 100px;
}
ol {
  display: flex;
  list-style: none;
  align-items: center;
  position: relative;
}
ol li {
  margin: 0px 5px;
}
ol li a {
  padding: 10px;
  color: var(--lightest-slate);
}
a {
  color: inherit;
}
