.aboutme-info {
  display: flex;
  justify-content: center;
  color: white;
}
.aboutme__info__section {
  color: var(--lightest-slate);
}
.aboutme__intro {
  color: var(--green);
  font-size: 30px;
  margin: 0;
}
.header__big {
  font-size: clamp(40px, 8vw, 80px);
  margin: 0;
}
h2 {
  color: white;
  padding-left: 35px;
  line-height: 1.1;
}
h3 {
  line-height: 0.9;
}
p {
  max-width: 690px;
  margin: 0px;
  font-size: 20px;
  margin-top: 20px;
}
.common{
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    height: 100vh;
    justify-content: center;
    align-items: flex-start;
}
